:root {
  --font-primary: "Oswald", "Arial", sans-serif;
  --font-secondary: "Lato", "Arial", sans-serif;
  --color-font-basic: #444444;
  --color-font-second: #000000;
  --color-font-second-hover: rgba(0, 0, 0, 0.6);
  --color-font-second-active: rgba(0, 0, 0, 0.3);
  --color-font-third: #111111;
  --color-font-third-hover: #3e3e3e;
  --color-font-third-active: #6b6b6b;
  --color-font-card: #222222;
  --color-font-bgnd: #ffffff;
  --color-font-bgnd-hover: rgba(255, 255, 255, 0.6);
  --color-font-bgnd-active: rgba(255, 255, 255, 0.3);
  --color-font-counter: #f2f2f2;
  --color-bgnd-basic: #ffffff;
  --color-bgnd-second: #f2f2f2;
  --color-bgnd-image: #68b738;
  --color-bgnd-example: #eaeaea;
  --color-btn-basic: #68b738;
  --color-btn-basic-hover: #5eaa2f;
  --color-btn-more: #f2f2f2;
  --color-brdr-basic: #d9d9d9;
  --color-brdr-second: #cdcdcd;
  --color-brdr-third: #e6e6e6;
  --color-brdr-input: #e7e7e7;
  --color-brdr-card: #ebebeb;
  --color-brdr-link: #68b738;
  --color-brdr-bgnd: #ffffff;
  --color-brdr-input-focus: #444444;
  --color-brdr-input-error: #ff8282;
  --color-brdr-radio-hover: #bababa;
  --color-brdr-radio-hover-opacity: rgba(186, 186, 186, 0.3);
  --color-icon-basic: #000000;
  --color-icon-second: #666666;
  --color-icon-hover: #68b738;
  --color-radio-marker: #68b738;
  --padding-page: calc(20px + 0.0223214286 * (100vw - 320px));
}
@media (width >= 768px) {
  :root {
    --padding-page: calc(30px + 0.119047619 * (100vw - 768px));
  }
}
@media (width >= 1440px) {
  :root {
    --padding-page: 110px;
  }
}
@media (width >= 1680px) {
  :root {
    --padding-page: calc(50vw - 730px);
  }
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-primary);
  font-size: 16px;
  line-height: 18px;
  color: var(--color-font-basic);
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-bgnd-basic);
}

.page__header {
  margin-bottom: 27px;
}
@media (width >= 768px) {
  .page__header {
    margin-bottom: 40px;
  }
}
@media (width >= 768px) {
  .page__header {
    margin-bottom: 19px;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
}

.header {
  position: relative;
  display: block;
}
@media (width >= 768px) {
  .header {
    padding-right: var(--padding-page);
    padding-left: var(--padding-page);
    display: grid;
    grid-template-columns: min-content 1fr;
    align-items: flex-start;
    padding-top: 25px;
  }
}
@media (width >= 1440px) {
  .header {
    padding: 0;
  }
}
@media (width < 768px) {
  .header--js-menu_closed {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--color-brdr-third);
  }
  .header--js-menu_closed.header--index {
    border-bottom: none;
  }
}
@media (width < 768px) {
  .header--js-menu_opened {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
}
.header--index {
  margin: 0;
}
@media (width >= 1440px) {
  .header--index {
    grid-template-columns: 50% 50%;
    margin-bottom: 82px;
  }
  .header--index::before {
    content: "";
    z-index: -1;
    display: block;
    grid-area: 1/2/3/3;
    width: 100%;
    height: 100%;
    background-color: #62a53a;
    background-image: url("../img/background-header.jpg");
    background-position: left center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.header__logo {
  display: inline-flex;
  align-items: center;
  padding: 13px 20px 14px;
}
@media (width >= 768px) {
  .header__logo {
    row-gap: 13px;
    column-gap: 13px;
    justify-content: start;
    margin-right: 32px;
    padding: 0;
  }
}
@media (width >= 1440px) {
  .header__logo {
    grid-area: 1/1/2/2;
    row-gap: 14px;
    column-gap: 14px;
    width: fit-content;
    margin: 0;
    margin-top: 55px;
    margin-left: var(--padding-page);
  }
}

.header__wordmark {
  position: absolute;
  right: var(--padding-page);
  height: fit-content;
  padding-block: 23px;
}
@media (width >= 768px) {
  .header__wordmark {
    position: static;
    padding: 0;
  }
}
@media (width < 768px) {
  .header--js-menu_closed .header__wordmark, .header--js-menu_opened .header__wordmark {
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__toggle {
  display: none;
  width: 44px;
  height: 44px;
  padding: 0;
  background-color: var(--color-bgnd-basic);
  border: none;
  cursor: pointer;
}
@media (width < 768px) {
  .header--js-menu_closed .header__toggle {
    display: inline-flex;
    margin-right: 11px;
    padding: 10px;
  }
  .header--js-menu_closed .header__toggle::before {
    content: "";
    width: 24px;
    height: 2px;
    margin-top: 2px;
    background-color: var(--color-icon-basic);
    box-shadow: 0 8px 0 0 var(--color-icon-basic), 0 16px 0 0 var(--color-icon-basic);
  }
  .header--js-menu_opened .header__toggle {
    display: inline-grid;
    margin-right: 7px;
  }
  .header--js-menu_opened .header__toggle::before, .header--js-menu_opened .header__toggle::after {
    content: "";
    grid-area: 1/1/2/2;
    width: 25px;
    height: 2px;
    margin: auto;
    background-color: var(--color-icon-basic);
  }
  .header--js-menu_opened .header__toggle::before {
    transform: rotate(45deg);
  }
  .header--js-menu_opened .header__toggle::after {
    transform: rotate(-45deg);
  }
}
@media (hover: hover) {
  .header__toggle:hover::before, .header__toggle:hover::after, .header__toggle:focus-visible::before, .header__toggle:focus-visible::after {
    opacity: 0.6;
  }
}
.header__toggle:active::before, .header__toggle:active::after {
  opacity: 0.3;
}

@media (width >= 768px) {
  .header__nav {
    justify-self: end;
  }
}
@media (width >= 1440px) {
  .header__nav {
    grid-area: 1/2/2/3;
    padding-inline: 33px var(--padding-page);
    padding-top: 55px;
  }
}
@media (width < 768px) {
  .header--js-menu_closed .header__nav {
    display: none;
  }
  .header--js-menu_opened .header__nav {
    width: 100%;
  }
}

@media (width < 768px) {
  .header--js-menu_opened .header__list {
    position: absolute;
    right: 0;
    left: 0;
  }
}

.header__intro {
  width: 100%;
}
@media (width >= 768px) {
  .header__intro {
    grid-area: 2/1/3/3;
  }
}

.container {
  padding-right: var(--padding-page);
  padding-left: var(--padding-page);
}

.logo {
  text-decoration: none;
}
@media (hover: hover) {
  .logo:hover .logo__icon, .logo:hover .logo__wordmark, .logo:focus-visible .logo__icon, .logo:focus-visible .logo__wordmark {
    opacity: 0.8;
  }
}
.logo:active .logo__icon, .logo:active .logo__wordmark {
  opacity: 0.6;
}

@media (width >= 768px) {
  .logo__icon {
    width: 60px;
    height: 50px;
  }
}
@media (width >= 1440px) {
  .logo__icon {
    width: 70px;
    height: 59px;
  }
}

.logo__wordmark {
  display: block;
  fill: var(--color-font-third);
}
@media (width >= 768px) {
  .logo__wordmark {
    fill: var(--color-font-basic);
  }
}
@media (width >= 1440px) {
  .logo__wordmark {
    width: 118px;
    height: 21px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: var(--color-bgnd-basic);
  border-top: 1px solid var(--color-brdr-third);
}
@media (width >= 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 33px;
    margin-bottom: 65px;
    border-top: none;
  }
}
@media (width >= 1440px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 33px;
    margin: 0;
    background-color: transparent;
  }
}

.nav__item {
  text-align: center;
  border-bottom: 1px solid var(--color-brdr-third);
}
@media (width >= 768px) {
  .nav__item {
    border-bottom: none;
  }
}

.nav__link {
  display: block;
  width: fit-content;
  margin: 0 auto;
  padding-block: 22px 23px;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-font-second);
}
@media (width >= 768px) {
  .nav__link {
    padding-block: 13px;
    font-size: 18px;
    line-height: 24px;
  }
  .nav__link--current::after {
    content: "";
    display: block;
    width: calc(100% - 2px);
    margin: 0 auto;
    box-shadow: 0 8px 0 1px var(--color-brdr-link);
  }
}
@media (width >= 1440px) {
  .nav__link {
    padding-block: 16px;
    font-size: 20px;
    line-height: 30px;
  }
  .header--index .nav__link {
    color: var(--color-font-bgnd);
  }
}
@media (width >= 1440px) and (hover: hover) {
  .header--index .nav__link:not(span):hover, .header--index .nav__link:not(span):focus-visible {
    color: var(--color-font-bgnd-hover);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link:not(span):active {
    color: var(--color-font-bgnd-active);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link--current::after {
    box-shadow: 0 8px 0 1px var(--color-brdr-bgnd);
  }
}
@media (hover: hover) {
  .nav__link:not(span):hover, .nav__link:not(span):focus-visible {
    color: var(--color-font-second-hover);
  }
}
.nav__link:not(span):active {
  color: var(--color-font-second-active);
}

.main-index__product-series {
  margin-bottom: 20px;
}
@media (width >= 768px) {
  .main-index__product-series {
    margin-bottom: 45px;
  }
}
@media (width >= 1440px) {
  .main-index__product-series {
    margin-bottom: 71px;
  }
}

.main-index__details {
  margin-bottom: 29px;
}
@media (width >= 768px) {
  .main-index__details {
    margin-bottom: 46px;
  }
}
@media (width >= 768px) {
  .main-index__details {
    margin-bottom: 69px;
  }
}

.intro {
  display: grid;
  place-items: center;
  padding-bottom: 20px;
  text-align: center;
}
@media (width >= 768px) {
  .intro {
    place-items: start;
    padding: 0;
    padding-inline: 90px;
    text-align: start;
  }
}
@media (width >= 1440px) {
  .intro {
    grid-template-rows: min-content min-content 1fr;
    grid-template-columns: 1fr 1fr;
    padding: 26px var(--padding-page) 52px;
  }
}
.intro::before {
  content: "";
  display: block;
  grid-area: 1/1/3/2;
  width: 100%;
  height: 100%;
  min-height: 302px;
  background-image: url("../img/background-intro.jpg"), linear-gradient(to right, #64a73c calc(50vw - 30px), #62a53a calc(50vw + 30px));
  background-position: top;
  background-size: auto 100%;
  background-repeat: no-repeat;
}
@media (width >= 768px) {
  .intro::before {
    display: none;
  }
}

.intro__title {
  padding-right: var(--padding-page);
  padding-left: var(--padding-page);
  grid-area: 1/1/2/2;
  margin: 0;
  margin-bottom: 25px;
  padding-top: 27px;
  color: var(--color-font-bgnd);
}
@media (width >= 768px) {
  .intro__title {
    margin-bottom: 40px;
    padding: 0;
    color: var(--color-font-second);
  }
}
@media (width >= 1440px) {
  .intro__title {
    grid-column: 1/2;
    margin-inline: 80px;
    margin-top: 81px;
  }
}

.intro__slogan {
  padding-right: var(--padding-page);
  padding-left: var(--padding-page);
  grid-area: 2/1/3/2;
  margin: 0;
  padding-bottom: 164px;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: var(--color-font-bgnd);
}
@media (width >= 768px) {
  .intro__slogan {
    margin-bottom: 21px;
    padding: 0;
    font-size: 20px;
    line-height: 20px;
    color: var(--color-font-second);
  }
}
@media (width >= 1440px) {
  .intro__slogan {
    grid-column: 1/2;
    margin-inline: 80px;
    margin-bottom: 52px;
  }
}

.intro__image {
  margin-top: -135px;
  margin-bottom: 3px;
}
@media (width >= 768px) {
  .intro__image {
    grid-area: 3/1/4/2;
    justify-self: center;
    margin: 0;
  }
}
@media (width >= 1440px) {
  .intro__image {
    z-index: -1;
    grid-area: 1/2/4/3;
    align-self: center;
    justify-self: start;
    margin: 0;
    margin-left: -163px;
  }
}

.intro__link {
  width: calc(100% - 2 * var(--padding-page));
  max-width: 400px;
}
@media (width >= 768px) {
  .intro__link {
    grid-area: 3/1/4/2;
    width: auto;
    max-width: 264px;
    margin-top: 29px;
    margin-left: calc(-354px + 50vw - var(--padding-page));
  }
}
@media (width >= 1440px) {
  .intro__link {
    grid-column: 1/2;
    max-width: none;
    margin: 0;
    margin-inline: 80px;
  }
}

.heading {
  font-size: 36px;
  line-height: 36px;
  font-weight: normal;
}
@media (width >= 768px) {
  .heading {
    font-size: 60px;
    line-height: 60px;
  }
}
.heading--subhead {
  position: relative;
  margin-right: var(--padding-page);
  padding-inline: var(--padding-page) 20px;
  font-size: 24px;
  line-height: 26px;
  background-color: var(--color-bgnd-basic);
}
@media (width >= 768px) {
  .heading--subhead {
    margin-inline: var(--padding-page);
    padding-inline: 40px;
    font-size: 36px;
    line-height: 36px;
  }
}
.heading--subhead::before {
  content: "";
  position: absolute;
  top: 16px;
  left: 0;
  z-index: -1;
  width: 100vw;
  height: 2px;
  background-color: var(--color-bgnd-image);
}
@media (width >= 768px) {
  .heading--subhead::before {
    top: 22px;
    left: calc(40px - var(--padding-page));
  }
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-family: var(--font-primary);
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-font-bgnd);
  background-color: var(--color-btn-basic);
  border: none;
  cursor: pointer;
}
.button--font_intro {
  padding: 11px 10px;
  font-size: 14px;
  line-height: 18px;
}
@media (width >= 768px) {
  .button--font_intro {
    padding: 14px 26px;
    font-size: 20px;
    line-height: 20px;
  }
}
.button--more {
  color: var(--color-font-basic);
  background-color: var(--color-btn-more);
}
.button--more:disabled {
  color: var(--color-font-basic);
  background-color: var(--color-btn-more);
  opacity: 0.3;
  cursor: initial;
}
@media (hover: hover) {
  .button:hover, .button:focus-visible {
    background-color: var(--color-btn-basic-hover);
  }
}
.button:active {
  color: var(--color-font-bgnd-active);
  background-color: var(--color-btn-basic-hover);
}
.button:disabled {
  color: var(--color-font-bgnd);
  background-color: var(--color-btn-basic);
  opacity: 0.3;
  cursor: initial;
}

.product-series__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}
@media (width >= 768px) {
  .product-series__list {
    row-gap: 30px;
  }
}
@media (width >= 1440px) {
  .product-series__list {
    flex-direction: row;
    flex-wrap: wrap;
    row-gap: 40px;
    column-gap: 80px;
    justify-content: center;
  }
}

.product-series__item {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 20px;
  align-items: center;
  padding: 24px 20px 18px;
  background-color: var(--color-bgnd-second);
