  border-color: var(--color-brdr-radio-hover-opacity);
}
.radio:active .radio__input:not(:disabled)::before {
  opacity: 0.3;
}
.radio:active .radio__input:not(:disabled) + .radio__text {
  opacity: 0.3;
}
.radio__input:disabled {
  opacity: 0.3;
}
.radio__input:disabled + .radio__text {
  opacity: 0.3;
}
.radio__input:checked::before {
  content: "";
  display: block;
  width: 13px;
  height: 13px;
  margin-top: 50%;
  margin-left: 50%;
  background-color: var(--color-radio-marker);
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
@media (width >= 768px) {
  .radio__input:checked::before {
    width: 18px;
    height: 18px;
  }
}

.radio__text {
